export default {
	data() {
		return {
			countName: '',
			password: ''
		}
	},
	methods: {
		goToRegister() {
			uni.redirectTo({
				url: "../register/register"
			})
		},
		login() {
			var that = this
			uni.request({
				url: this.baseUrl + 'login/login',
				data: {
					mobile: that.countName,
					password: that.password
				},
				method: "POST",
				header: {
					'content-type': 'application/x-www-form-urlencoded',
				},
				success(res) {
					if (res.data.code == 1) {
						// console.log(res.data.data)
						uni.setStorage({
							key:'userInfo',
							data: res.data.data,
							success() {
								uni.showToast({
									title: res.data.msg,
									icon: 'none',
									duration: 1000,
									success() {
										setTimeout(function() {
											//要延时执行的代码
											uni.switchTab({
												url: '../index/index'
											});
										}, 1000)
										
									}
								})
							}
						})
						

					} else {
						uni.showToast({
							title: res.data.msg,
							icon: 'none',
							duration: 2000
						})
					}
				}
			})
		}
	}
}
